import { StartStreamConfig } from '../definitions';
import { SourceSet } from './source-set';

const defProtocols = ['jpeg', 'hls', 'rtmp', 'mp3', 'vorbis', 'webrtc'];

export function getSourceSet(
    vv: any,
    av: any,
    config: StartStreamConfig,
    protocols: string[] = defProtocols
): SourceSet {
    const videoDataUrl: string = vv.dataURL;
    const audioDataUrl: string = av.dataURL;

    const isMobile = config.mobile;

    const sourceSet: SourceSet = {};

    if (protocols.includes('jpeg') || protocols.includes('mjpeg')) {
        getJpegSources(
            config,
            protocols,
            vv,
            isMobile,
            sourceSet,
            videoDataUrl
        );
    }
    if (protocols.includes('hls')) {
        getHlsSource(isMobile, vv, sourceSet);
    }
    if (protocols.includes('rtmp')) {
        getRtmpSource(sourceSet, vv);
    }
    if (protocols.includes('webrtc')) {
        getWebRtcSource(sourceSet, vv);
    }

    if (vv.hlsURL && protocols.includes('hls')) {
        sourceSet.hls.unshift({ stream: vv.hlsURL });
        sourceSet.preferredOrder = ['hls', 'hds', 'rtmp', 'mjpeg', 'jpeg'];
        if (!vv.hdsURL) {
            sourceSet.preferredOrder.splice(1, 1);
        }
    }

    if (av && audioDataUrl) {
        getAudioSources(protocols, sourceSet, audioDataUrl);
    }

    return sourceSet;
}

function getAudioSources(
    protocols: string[],
    sourceSet: SourceSet,
    aDataUrl: string
): void {
    if (protocols.includes('mp3')) {
        getMp3Source(sourceSet, aDataUrl);
    }
    if (protocols.includes('vorbis')) {
        getVorbisSource(sourceSet, aDataUrl);
    }
}

function getWebRtcSource(sourceSet: SourceSet, vv: any): void {
    if (vv.mediaWebrtcUrl) {
        sourceSet.webrtc = [
            {
                stream: vv.mediaWebrtcUrl
            }
        ];
    }
}

function getVorbisSource(sourceSet: SourceSet, aDataUrl: string): void {
    sourceSet.vorbis = [
        {
            stream: aDataUrl + '&format=13&f=stream.ogg'
        }
    ];
}

function getMp3Source(sourceSet: SourceSet, aDataUrl: string): void {
    sourceSet.mp3 = [
        {
            stream: aDataUrl + '&format=8&f=stream.mp3'
        }
    ];
}

function getJpegSources(
    config: StartStreamConfig,
    protocols: string[],
    vv: any,
    isMobile: boolean,
    sourceSet: SourceSet,
    vDataUrl: string
): void {
    const maxFps = config.maxFps > 0 ? config.maxFps : 15;
    let params = '';
    if (config.width) {
        params += `&width=${config.width}`;
    }
    if (config.height) {
        params += `&height=${config.height}`;
    }
    if (config.resizeMode) {
        params += `&sizemode=${config.resizeMode}`;
    }
    if (protocols.includes('jpeg')) {
        getJpegSource(vv, isMobile, params, sourceSet);
    }
    if (protocols.includes('mjpeg')) {
        getMJpegSource(sourceSet, vDataUrl, isMobile, maxFps, params);
    }
}

function getRtmpSource(sourceSet: SourceSet, vv: any): void {
    if (vv.mediaRtmpUrl) {
        const rtmpUrl = vv.mediaRtmpUrl;
        const rtmpUrlPattern = /(.*)\/(\d+)/;

        const rtmp = rtmpUrlPattern.exec(rtmpUrl);

        if (rtmp) {
            sourceSet.rtmp = [
                {
                    app: rtmp[1],
                    stream: rtmp[2]
                }
            ];
        }
    }
}

function getHlsSource(isMobile: boolean, vv: any, sourceSet: SourceSet): void {
    let hlsStreamUrl: string;
    if (isMobile) {
        hlsStreamUrl = vv.mediaHlsMobileUrl;
    } else {
        hlsStreamUrl = vv.mediaHlsUrl;
    }

    if (hlsStreamUrl) {
        sourceSet.hls = [
            {
                stream: hlsStreamUrl
            }
        ];
    }
}

function getMJpegSource(
    sourceSet: SourceSet,
    vDataUrl: string,
    isMobile: boolean,
    maxFps: number,
    params: string
): void {
    sourceSet.mjpeg = [
        {
            stream:
                vDataUrl +
                (isMobile ? '&format=42' : '&format=20&quality=80') +
                `&fps=${maxFps}${params}`
        }
    ];
}

function getJpegSource(
    vv: any,
    isMobile: boolean,
    params: string,
    sourceSet: SourceSet
): void {
    if (vv.mediaJpegUrl) {
        const jpegSource =
            vv.mediaJpegUrl +
            (isMobile ? '&format=43' : '&format=15&quality=80') +
            params;
        sourceSet.jpeg = [
            {
                stream: jpegSource
            }
        ];
    }
}
