import Connection from './connection';
import {
    Abilities,
    ChargeInfo,
    ChatConfig,
    ChatEventMap,
    ChatHandler,
    Intent,
    Limits,
    Query,
    SourceMetrics,
    StartStreamConfig,
    UploadResult
} from './definitions';
import { ExitCode } from './exit-code';
import { Handler } from './handler';
import { logger } from './logging/logger';
import Response from './response';
import { SourceSet } from './sources/source-set';
import { getSourceSet } from './sources/source-set-parser';
import { TargetSet } from './targets/target-set';
import { VERSION } from './version';
import { Emoji } from './emoji';
import { Translations } from './translations';

const BASE_URL = 'https://vchat.farm1.campoints.net/proxy/';
const CONTROL_SERVLET_URL = 'https://control.farm1.campoints.net/VXControl';

const dataUrlPattern = /(ds[^.]*\.farm1\.campoints\.net).+?dataID=(\d+)/;

function NOT_AVAILABLE(): Promise<any> {
    return Promise.reject({ code: 4000, reason: 'not available' });
}

function sendTelemetry(type: string, params: any, async = true): void {
    logger.log('sendTelemetry.sendTelemetry', type, params);

    let url = `${BASE_URL}clientinfo.php?type=${type}`;
    if (params) {
        for (const key in params) {
            if (Object.prototype.hasOwnProperty.call(params, key)) {
                url += `&${key}=` + encodeURIComponent(params[key]);
            }
        }
    }

    if (type === 'unload' && 'function' === typeof navigator.sendBeacon) {
        navigator.sendBeacon(url);
    } else {
        /* eslint-disable no-undef */
        const ajax = (window as any).XMLHttpRequest
            ? new XMLHttpRequest()
            : new ActiveXObject('Microsoft.XMLHTTP');
        /* eslint-enable no-undef */
        if (ajax) {
            ajax.open('GET', url, async);
            ajax.send(null);
        }
    }
}

export function getProtocols(
    included?: string[],
    excluded?: string[]
): string[] {
    const include = included ? included : [
        'jpeg',
        'mjpeg',
        'hls',
        'hds',
        'rtmp',
        'ogg',
        'mp3',
        'vorbis',
        'webrtc'
    ];

    const exclude = excluded ? excluded : [];

    return include.filter((v) => !exclude.includes(v));
}

export class Chat implements EventTarget {
    // dummies
    public addEventListener: <K extends keyof ChatEventMap>(
        type: K,
        listener: (this: Chat, ev: ChatEventMap[K]) => any,
        useCapture?: boolean
    ) => void;
    public removeEventListener: (
        type: string,
        listener?: EventListenerOrEventListenerObject,
        useCapture?: boolean
    ) => void;
    public dispatchEvent: (evt: Event) => boolean;

    public host: {
        name: string;
        imageSrc: string;
    };

    /**
     * A target URL for uploading images within the chat.
     *
     * @deprecated Use the sendImage function instead of uploading directly to this url. Will be removed in later versions
     */
    public uploadUrl: string;
    public emojiList: Emoji[];
    public translations: Translations;

    public singleMode: boolean;
    /** When set to true the sender has his or her microphone muted via the sender software. */
    public audioMuted: boolean;
    /** When set to true the sender has blocked the guest from writing. */
    public textMuted: boolean;
    public paused: boolean;

    /**
     * List (object) of abilities. All abilities are boolean values indicating whether or not the current stream offers a certain feature.
     */
    public abilities: Abilities;

    private config: ChatConfig;
    private handler: ChatHandler;
    // The actual connection to the control servlet (streaming backend).
    private connection: Connection;
    private pendingResume: any;
    private startStreamConfig: StartStreamConfig;
    private queryingVideoPull: any;
    private queue = [];
    private initState = -1;
    private protocols: string[];

    // Set to true when the chat has been initialized successfully.
    private active = false;
    private uploadMediaUrl: string;

    constructor(config: ChatConfig, handler?: ChatHandler) {
        if (!config.clientId) {
            throw new Error('empty clientId');
        }
        if (!config.host) {
            throw new Error('empty host');
        }
        if (!config.version) {
            throw new Error('empty version');
        }
        if (config.version.length > 10) {
            throw new Error("max length of the property 'version' is 10");
        }

        this.config = config;

        if (handler) {
            this.handler = handler;
        } else {
            const h = new Handler(this);
            if (config.pauseSupport) {
                h.onChatPause = h.onChatPauseHandler;
            }
            const delegate = document.createDocumentFragment();
            [
                'addEventListener',
                'dispatchEvent',
                'removeEventListener'
            ].forEach((f) => (this[f] = (...xs): void => delegate[f](...xs)));
        }

        this.connection = new Connection(
            config.clientId,
            config.host,
            config.controlServletURL || CONTROL_SERVLET_URL,
            config.forceLongPooling
        );
    }

    public init(): Promise<{ intent: Intent; limits: Limits }> {
        if (this.initState !== -1) {
            return NOT_AVAILABLE();
        }
        this.initState = 0;

        sendTelemetry('init', {
            chtmnumber: this.config.clientId,
            UAC: navigator.appName,
            UAV: navigator.appVersion,
            UAO: navigator.platform,
            AG1: 'dhtml',
            SCR: `${screen.width}x${screen.height} ${screen.availWidth}x${screen.availHeight}`
        });

        const playerVersion = this.config.playerVersion
            ? `/P/${this.config.playerVersion}`
            : '';
        const version = `C/${VERSION}${playerVersion}/${this.config.version}`;
        const messengerProperties = this.config.messengerProperties
            ? JSON.stringify(this.config.messengerProperties)
            : null;

        return this._sendCommand(
            'CMDP_CINIT',
            { version, initData: this.config.initData, messengerProperties },
            (response) => {
                logger.log('INIT:', response.values);

                this.initState = 1;
                this.active = true;

                const values = response.values;

                this.host = {
                    name: values.hostlogin,
                    imageSrc: `${BASE_URL}image.php?performer=${values.hostlogin}`
                };

                this.abilities = {
                    text: values.canText == 1,
                    video: values.canVideo == 1,
                    audio: values.hasAudio == 1,
                    preview: values.canPreview == 1,
                    upstream: values.canCam2Cam == 1, // isn't set yet on chat init
                    single: values.canSingle == 1,
                    tip: values.canTip == 1
                };

                const limits: Limits = {
                    total: this._getLimit(values, 'timelimit'),
                    video: this._getLimit(values, 'videolimit'),
                    text: this._getLimit(values, 'textlimit'),
                    preview: this._getLimit(values, 'previewlimit')
                };

                const t: string = (values.chattype || '').toLowerCase();
                const intent = {
                    text: t === 'text' || t === 'video',
                    video: t === 'voyeur' || t === 'video',
                    audio:
                        (t === 'voyeur' || t === 'video') &&
                        this.abilities.audio,
                    preview: limits.preview > 0
                };

                this.protocols = getProtocols(
                    (values.included) ? values.included.split(',') : null,
                    (values.excluded) ? values.excluded.split(',') : null
                );

                if (!this.config.ignoreUnload) {
                    window.addEventListener('beforeunload', this.unloadHandler);
                    window.addEventListener('unload', this.unloadHandler);
                }

                this.audioMuted = values.audioMuted == 1;
                this.uploadUrl = values.uploadUrl;
                this.uploadMediaUrl = values.uploadMediaUrl;

                if (values.emojiList) {
                    this.emojiList = JSON.parse(values.emojiList);
                }
                if (values.translations) {
                    this.translations = JSON.parse(values.translations);
                }

                this.connection.startNoop(this._noopCommandsHandler.bind(this));
                if (this.queue.length > 0) {
                    this.queue.forEach((cmd) => cmd());
                    this.queue = [];
                }
                return { intent: intent, limits: limits };
            },
            (): void => {
                this.initState = -1;
            }
        );
    }

    /**
     * Ends the chat with the given exitCode and closes the websocket connection, tears down the whole chat setup.
     * @param exitCode - the exitCode send to the server as the close reason
     */
    public close(exitCode: number = ExitCode.USER): Promise<any> {
        if (!this.active) {
            return NOT_AVAILABLE();
        }

        setTimeout(this._close.bind(this, exitCode), 500);

        return new Promise((resolve) => {
            this.connection.send('CMDP_CLOGOUT', { exitCode: exitCode }, () => {
                this._close(exitCode);
                resolve();
            });
        });
    }

    /**
     * This starts the actual stream for the current chat and returns a list of sources that can be passed to a player.
     * @param config
     * @returns A set of sources available for the given configuration / chat.
     */
    public startStream(config?: StartStreamConfig): Promise<SourceSet> {
        if (!config) {
            config = {};
        }

        if (config.type === 'preview') {
            return this._sendCommand('CMDP_CSTARTPREVIEW', {}, (response) => {
                const values = response.values;
                const protocolsWithoutAudio = this.protocols.filter(
                    (value) => value !== 'mp3' && value !== 'ogg'
                );
                return getSourceSet(
                    values,
                    values,
                    config,
                    protocolsWithoutAudio
                );
            });
        }

        this.startStreamConfig = config;
        const p = [];
        p.push(
            this._sendCommand(
                'CMDP_CSTARTVIDEOPULL',
                {},
                (response, resolve, reject) => {
                    if (response.values.state === 'querying') {
                        this.queryingVideoPull = { resolve, reject, config };
                    } else {
                        return response.values;
                    }
                }
            )
        );
        if (!config.noAudio) {
            p.push(
                new Promise((resolve) => {
                    this._sendCommand(
                        'CMDP_CSTARTAUDIOPULL',
                        {},
                        (response) => response.values
                    )
                        .then((values) => resolve(values))
                        .catch(() => resolve({}));
                })
            );
        }

        return Promise.all(p).then((values) =>
            getSourceSet(values[0], values[1] || {}, config, this.protocols)
        );
    }

    /**
     * Starts the text stream part of a chat. Text streams are part of LivePreviews and full video chats. If the text stream is not running but the video stream is, the guest is in voyeur mode.
     */
    public startText(): Promise<any> {
        return this._sendCommand('CMDP_CSTARTTEXT');
    }

    /**
     * Starts an upstream (cam 2 cam) part for the chat
     *
     * @returns a list of possible upstream targets
     */
    public startUpstream(): Promise<TargetSet> {
        return this._sendCommand(
            'CMDP_CSTARTVIDEOPUSH',
            { flashclient: true },
            (response) => {
                const dataUrl = response.values.dataURL;
                const parts = dataUrlPattern.exec(dataUrl);

                return {
                    jpeg: [
                        {
                            stream: dataUrl,
                            width: parseInt(response.values.width),
                            height: parseInt(response.values.height),
                            fps: parseFloat(response.values.fps),
                            quality: parseFloat(response.values.quality) / 100
                        }
                    ],
                    rtmp: [
                        {
                            app: `rtmp://${parts[1]}/vxstream`,
                            stream: parts[2]
                        }
                    ]
                };
            }
        );
    }

    /**
     * Stops cam 2 cam
     */
    public stopUpstream(): Promise<any> {
        return this._sendCommand('CMDP_CSTOPVIDEOPUSH');
    }

    /**
     * Starts the private chat mode
     */
    public startSingle(): Promise<any> {
        return this.abilities.single
            ? this._sendCommand('CMDP_CQUERY_SINGLE')
            : NOT_AVAILABLE();
    }

    /**
     * @deprecated use sendMediaFile
     */
    public sendImageFormData(data: FormData): Promise<UploadResult> {
        return this.sendFormData(this.uploadUrl, data);
    }
    0;
    /**
     * @deprecated use sendMediaFile
     */
    public sendImageFile(file: File): Promise<UploadResult> {
        const formData = new FormData();
        formData.append(file.name, file, file.name);
        return this.sendImageFormData(formData);
    }

    private async sendFormData(
        url: string,
        data: FormData
    ): Promise<UploadResult> {
        try {
            const response = await fetch(url, {
                method: 'POST',
                body: data
            });
            const responseText = await response.text();
            return {
                error: responseText === 'OK' ? null : responseText,
                successfull: responseText === 'OK'
            };
        } catch (error) {
            return {
                successfull: false,
                error: error.message
            };
        }
    }

    /**
     * send media file. For blob upload use new File([blob], filename)
     * @param file
     * @param messageKey
     * @returns UploadResult
     */
    public async sendMediaFile(
        file: File,
        messageKey: string
    ): Promise<UploadResult> {
        const formData = new FormData();
        formData.append(file.name, file, file.name);
        /* todo: change to CMDP_UPLOADMEDIA, if implemented on the server side*/
        const url =
            this.uploadMediaUrl +
            '&action=CMDP_CUPLOADPIC' +
            '&messageKey=' +
            encodeURIComponent(messageKey) +
            '&format=json' +
            '&strip=true';
        try {
            const response = await fetch(url, {
                method: 'POST',
                body: formData
            });
            const responseText = await response.text();
            const responseObj = JSON.parse(responseText);
            const success: boolean = responseObj.result.code === 2000;
            return {
                successfull: success,
                error: success ? null : responseObj.result.reason
            };
        } catch (error) {
            return {
                successfull: false,
                error: error.message
            };
        }
    }

    /**
     * Sends a message from the guest to the backend to be displayed in the chat window of the sender.
     *
     * @param text - The message send to the sender
     * @param messageKey
     */
    public sendMessage(text: string, messageKey?: string): Promise<any> {
        return this._sendCommand('CMDP_CMSG', {
            text: text,
            messageKey: messageKey
        });
    }
    /**
     *  Sends a response to a query (like a private chat request) where key is a string (indicating the actual query) and the value can be of any type.
     * @param key
     * @param response
     */
    public sendQueryResponse(key: string, response: any): Promise<any> {
        return this._sendCommand('CMDP_CQUERYUSER', {
            key: key,
            response: response
        });
    }

    /**
     * Triggers a tip with given amount (cents) on the backend. The remaining video time limit will be adjusted accordingly. The sender will be informed about the tip via a message in the chat window. The gift parameter is an optional string indicating a certain gift type (deprecated).
     *
     * @param amount
     * @param gift - @deprecated
     */
    public sendTip(amount = 100, gift = ''): Promise<any> {
        return this.abilities.tip
            ? this._sendCommand('CMDP_CTIP', { cent: amount, gift: gift })
            : NOT_AVAILABLE();
    }

    /**
     * Sends the current audio state (muted / unmuted) to the backend to pass it on to the sender and inform him or her about whether or not the guest can actually hear them.
     *
     * @param enabled
     */
    public sendAudioState(enabled: boolean): Promise<any> {
        return this._sendCommand('CMDP_CSTATUS', {
            statusCode: enabled ? 11 : 12,
            text: enabled ? 'sound_enabled' : 'sound_disabled'
        });
    }

    /**
     * Sends an object to the backend for logging certain information.
     * @param metrics
     */
    public sendMetrics(metrics: SourceMetrics | any): void {
        if (Object.prototype.hasOwnProperty.call(metrics, 'protocol')) {
            sendTelemetry('update', {
                chtmnumber: this.config.clientId,
                DP: metrics.protocol
            });
        }

        // noinspection JSIgnoredPromiseFromCall
        this._sendCommand('CMDP_CSTATUS', {
            statusCode: 16,
            text: JSON.stringify(metrics)
        });
    }

    /**
     * Returns information about the one click payment feature
     */
    public getChargeInfo(): Promise<ChargeInfo> {
        return this._sendCommand('CMDP_CQUERY_CHARGE', {}, (response) => {
            const available =
                response.values.allowed == 'true' ||
                response.values.allowed == 1;
            const autoCharged = response.values.autoCharge || 0;
            return {
                available: available,
                amounts: available
                    ? response.values.amounts
                          .split(',')
                          .map((v: string) => parseInt(v, 10))
                    : [],
                currency: available ? response.values.currency : '',
                additionalCurrencies: available
                    ? JSON.parse(response.values.additionalCurrencies)
                    : [],
                autoCharged: autoCharged
            };
        });
    }

    /**
     * Triggers a one click payment with the given amount (cents).
     *
     * @param amount - The amount to charge
     */
    public sendCharge(amount: number): Promise<any> {
        return this._sendCommand(
            'CMDP_CCHARGE',
            { amount: amount },
            null,
            null,
            (response) => response.values.successfully == 'true'
        );
    }

    private _closeUnload(): void {
        if (this.active) {
            try {
                this.connection.send(
                    'CMDP_CLOGOUT',
                    { exitCode: ExitCode.UNLOAD },
                    () => {
                        /* noop */
                    }
                );
            } catch (error) {
                /* noop */
            }
        }
        this._close(ExitCode.UNLOAD, true, false);
    }

    private _close(
        exitCode: number,
        telemetry = true,
        closeConnection = true,
        exitMessage?: string
    ): void {
        if (!this.config.ignoreUnload) {
            window.removeEventListener('beforeunload', this.unloadHandler);
            window.removeEventListener('unload', this.unloadHandler);
        }

        if (this.active) {
            this.active = false;
            if (telemetry) {
                sendTelemetry(
                    'unload',
                    {
                        chtmnumber: this.config.clientId,
                        exitcode: exitCode,
                        comment: exitCode
                    },
                    false
                );
            }

            if (closeConnection) {
                this.connection.close();
            }

            this.handler.onChatStop(exitCode, exitMessage);
        }
    }

    private _noopCommandsHandler(response: Response): void {
        if (response.ok) {
            response.commands.forEach((command) =>
                this._processCommand(command)
            );
        } else if (response.code == 4104) {
            logger.warn('response.code == 4104');
            const searchResult = /exitCode=([\d-]+)/.exec(response.reason);
            const exitCode: number = searchResult
                ? parseInt(searchResult[1], 10)
                : ExitCode.DEFAULT;
            this._close(exitCode > 0 ? exitCode : ExitCode.DEFAULT, false);
        } else if (response === Response.TIMEOUT) {
            this._close(ExitCode.TIMEOUT);
        } else {
            logger.warn('response.code==', response.code);
        }
    }

    private _processCommand(command: {
        command: string;
        id: string;
        values: any;
    }): void {
        logger.log('processCommand', command);
        switch (command.command) {
            case 'CMSG': {
                const messageKey = command.values.messageKey;
                const paramKeys = [
                    'gift',
                    'tip',
                    'imgSrc',
                    'imgPixelated',
                    'audioOgg',
                    'audioMp3',
                    'audioM4a',
                    'videoPoster',
                    'videoHls',
                    'videoPosterPixelated',
                    'mediaType',
                    'mediaState',
                    'style',
                    'msgType'
                ];
                const params = {};

                for (const key of paramKeys) {
                    if (command.values[key]) {
                        params[key] = command.values[key];
                    }
                }

                this.handler.onMessage(
                    command.values.text,
                    command.values.from,
                    messageKey,
                    params
                );
                break;
            }

            case 'CEXIT': {
                this._close(
                    command.values.exitCode,
                    true,
                    true,
                    command.values.exitMessage
                );
                break;
            }
            case 'CQUERYUSER': {
                const choices = [];
                for (let i = 0; ; i++) {
                    const btn = `btn${i}`;
                    if (
                        !Object.prototype.hasOwnProperty.call(
                            command.values,
                            btn
                        )
                    ) {
                        break;
                    }
                    choices.push({
                        name: command.values[btn],
                        value: i === 0 ? 'no' : 'yes',
                        def: command.values.btndef == btn
                    });
                }

                const query: Query = {
                    // single|querysingle|...
                    key: command.values.key,
                    caption: command.values.caption,
                    text: command.values.text,
                    timeout: command.values.timeout * 1000,
                    choices: choices
                };
                if (
                    Object.prototype.hasOwnProperty.call(
                        command.values,
                        'price'
                    )
                ) {
                    query.price = parseInt(command.values.price, 10);
                }

                this.handler.onQuery(query);
                break;
            }

            case 'CSTARTVIDEOPULL': {
                if (this.queryingVideoPull) {
                    const q = this.queryingVideoPull;
                    this.queryingVideoPull = null;
                    q.resolve(command.values);
                } else {
                    this._resume(command.values, true);
                }
                break;
            }

            case 'CSTOPVIDEOPULL': {
                if (command.values.code == '4107') {
                    this._pause();
                } else {
                    logger.warn('unexpected CSTOPVIDEOPULL');
                    this._close(ExitCode.ERROR);
                }
                break;
            }

            case 'CSTOPPREVIEW': {
                this.handler.onLimitUpdate('preview', 0);
                break;
            }

            case 'CSTARTAUDIOPULL': {
                if (command.values.dataURL) {
                    this._resume(command.values, false);
                } else if (this.audioMuted) {
                    this.handler.onAudioMuteUpdate((this.audioMuted = false));
                }
                break;
            }

            case 'CSTOPAUDIOPULL': {
                if (command.values.code == '4107') {
                    this._pause();
                } else {
                    if (!this.audioMuted) {
                        this.handler.onAudioMuteUpdate(
                            (this.audioMuted = true)
                        );
                    }
                }
                break;
            }

            case 'CSTARTTEXT': {
                if (this.textMuted) {
                    this.handler.onTextMuteUpdate((this.textMuted = false));
                }
                break;
            }

            case 'CSTOPTEXT': {
                if (command.values.code == '4107') {
                    this._pause();
                } else {
                    if (!this.textMuted) {
                        this.handler.onTextMuteUpdate((this.textMuted = true));
                    }
                }
                break;
            }

            case 'UPDATE': {
                // handling limits update
                this._handleUpdate(command);

                break;
            }
            default: {
                logger.warn('unknown command');
            }
        }
    }

    private _handleUpdate(command: {
        command: string;
        id: string;
        values: any;
    }): void {
        if (this.handler.onLimitUpdate) {
            [
                ['timelimit_remain', 'total'],
                ['videolimit_remain', 'video'],
                ['textlimit_remain', 'text'],
                ['videolimit', 'videolimit']
            ].forEach((v) => {
                if (
                    Object.prototype.hasOwnProperty.call(command.values, v[0])
                ) {
                    const value = parseInt(command.values[v[0]], 10) * 1000;
                    if (!isNaN(value)) {
                        this.handler.onLimitUpdate(v[1] as keyof Limits, value);
                    }
                }
            });
        }

        if (
            this.handler.onVideoLimitWarningUpdate &&
            Object.prototype.hasOwnProperty.call(
                command.values,
                'videolimit_rest'
            )
        ) {
            const videolimitRest =
                parseInt(command.values.videolimit_rest, 10) * 1000;

            const belowThreshold = command.values.below_threshold === '1';

            this.handler.onVideoLimitWarningUpdate(
                belowThreshold,
                videolimitRest
            );
        }

        if (Object.prototype.hasOwnProperty.call(command.values, 'canSingle')) {
            const value = command.values.canSingle == 1;
            if (this.abilities.single !== value) {
                this.abilities.single = value;
                this.handler.onAbilityUpdate('single', value);
            }
        }
        if (Object.prototype.hasOwnProperty.call(command.values, 'canText')) {
            const value = command.values.canText == 1;
            if (this.abilities.text !== value) {
                this.abilities.text = value;
                this.handler.onAbilityUpdate('text', value);
            }
        }
        if (Object.prototype.hasOwnProperty.call(command.values, 'isSingle')) {
            const single = command.values.isSingle == 1;
            if (single !== this.singleMode) {
                this.handler.onSingleModeUpdate((this.singleMode = single));
            }
        }
        if (
            Object.prototype.hasOwnProperty.call(command.values, 'audioMuted')
        ) {
            const muted = command.values.audioMuted == 1;
            if (muted !== this.audioMuted) {
                this.handler.onAudioMuteUpdate((this.audioMuted = muted));
            }
        }
    }

    private _pause(): void {
        logger.log('Chat._pause');
        if (!this.paused) {
            if (this.handler.onChatPause) {
                this.paused = true;

                this.handler.onChatPause();
            } else {
                this._close(ExitCode.DEFAULT);
            }
        }
    }

    private _resume(values: any, video: boolean): void {
        if (!this.paused) {
            return;
        }

        let pr = this.pendingResume;
        if (!pr) {
            pr = this.pendingResume = {};
        }
        pr[video ? 'v' : 'a'] = values;

        const process = (): void => {
            if (pr.t) {
                clearTimeout(pr.t);
            }
            this.paused = false;
            if (this.handler.onChatResume) {
                this.handler.onChatResume(
                    getSourceSet(
                        pr.v,
                        pr.a || {},
                        this.startStreamConfig,
                        this.protocols
                    )
                );
            }
            this.pendingResume = null;
        };

        if (pr.v && pr.a) {
            process();
        } else {
            pr.t = setTimeout(() => process(), 100);
        }
    }

    private _sendCommand(
        command: string,
        params: any = {},
        successFunc?: (response: Response, res?, rej?) => any,
        failFunc?: (Response: Response) => any,
        testFunc?: (response: Response) => boolean
    ): Promise<any> {
        return new Promise((resolve, reject) => {
            const cmd = (): void => {
                this.connection.send(command, params, (response) => {
                    if (response.ok && (!testFunc || testFunc(response))) {
                        if (successFunc) {
                            const result = successFunc(
                                response,
                                resolve,
                                reject
                            );
                            if (result !== undefined) {
                                resolve(result);
                            }
                        } else {
                            resolve();
                        }
                    } else {
                        if (failFunc) {
                            failFunc(response);
                        }
                        reject({
                            code: response.code,
                            reason: response.reason
                        });
                    }
                });
            };
            if (this.initState === 1 || command === 'CMDP_CINIT') {
                cmd();
            } else {
                logger.log('queueing command', command);
                this.queue.push(cmd);
            }
        });
    }

    private unloadHandler = (): void => this._closeUnload();

    private _getLimit(pvalues, prop: string): number {
        return Object.prototype.hasOwnProperty.call(pvalues, prop)
            ? parseInt(pvalues[prop], 10) * 1000
            : -1;
    }
}
